:root {
  --primary-light: #8abdff;
  /* --primary-own: #5a84a2; */
  /* --primary-own:#373D3F; */
  --primary-own:#0D3D56;
  --primary-dark: #5b0eeb;
  
  --white: #FFF;
  --greyLight-1: #E4EBF5;
  --greyLight-2: #c8d0e7;
  --greyLight-3: #bec8e4;
  --greyDark: #9baacf;
  --greyDark-1: #BABECC;
  --background-1:#EBECF0;
  --loader:#ffff1a;

}
@media screen and (min-width:851px) {
  html{
    font-size: 62.5%;
}
  body{
    background-color: var(--background-1) !important;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  }
  .main-head{
    font-weight: 900;
    font-size: 3.5rem;
  }
.item-container{
  display: grid;
  grid-template-columns: 18% 18% 18% 18% 18%;
  grid-gap: 3rem;
  padding: 5rem;
  /* border: 2px solid; */
  /* text-align: center; */
}
.img{
  width: 100%;
  height: 200px;
  border-radius: 1.5rem;
  /* object-fit: cover; */
}
.ani-card{
  padding: 1rem;
  border-radius: 1rem;
  box-shadow: .3rem .3rem .6rem var(--greyDark-1), 
  -.2rem -.2rem .5rem var(--white);
  background: linear-gradient(140deg, rgba(235,236,240, 0.22), rgba(248, 247, 247, 0.25));
}
.heading{
  font-size: 1.7rem;
  color: var(--primary-own)
}
.input-main-container{
  padding-top: 0rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.input-container{
  width: 30%;
  height: 5%;
}

.email-container{
  display: flex;
} 
.email-icon{
  display: flex;
  justify-content: center;
  align-items: center;
  border:0.1rem solid;
  padding: 1rem;
  border-radius: 1rem 0rem 0rem 1rem;
  color: var(--primary-own);
  border-color: #dddddd;
}
.password-icon{
  position: absolute;
  right: 1.2rem;
  top:1.2rem;
  color: var(--primary-own)
}
.input-text{
  width: 20.4rem;
  height: 4rem;
  border: none;
  border-radius: 0rem 1rem 1rem 0rem;
  font-size: 1.4rem;
  padding-left: 2rem;
  background: none;
  font-family: inherit;
  color: var(--primary-own) ;
  box-shadow:inset .2rem .2rem .5rem var(--greyDark-1), 
              inset -.2rem -.2rem .5rem var(--white) !important; 
  outline: none;
  width: 100%;
  height: 4.5rem;    
  font-size: 2rem;           
} 
.input-text:focus{
  box-shadow:.3rem .3rem .6rem var(--greyDark-1), 
  -.2rem -.2rem .5rem var(--white) !important;  
}
::placeholder { 
  color: var(--greyDark) !important;
}
.pagination-container{
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 3rem;
}
.button-container{
  display: flex;
  justify-content: space-around;
}
.page-no{
  margin:0rem 1.5rem 0rem 1.5rem;
  color: var(--primary-own);
  font-size: 1.9rem;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
}
.total-pages{
  padding-left: 3rem;
  font-size: 1.7rem;
  color: var(--primary-own);
}
.btn{
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
  cursor: pointer;
  transition: .3s ease;
  border-radius: 2rem !important;
  box-shadow: .3rem .3rem .6rem var(--greyDark-1), 
  -.2rem -.2rem .5rem var(--white) !important;
  color: var(--primary-dark) !important;
  padding: 1rem 2rem 1rem 2rem !important;
 } 
.btn:hover{
  color: var(--primary-own) !important;
}
.btn:active{
  box-shadow: inset .2rem .2rem .5rem var(--greyDark-1), 
              inset -.2rem -.2rem .5rem var(--white) !important;
  /* border-color: var(--greyLight-2) !important;     */
}
.btn:focus{
  /* outline: none !important; */
  /* box-shadow: none !important; */
}
.btn-block{
  letter-spacing: 0.2rem !important;
  font-size: 1.6rem !important;
  font-weight: 800 !important;
}
.content-container{
  padding: 2rem;
}
.codded{
  text-align: center;
  padding: 1rem 0rem 5rem 0rem;
 color: gray;
}
.ring{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 15rem;
  height: 15rem;
  background:transparent;
  border-radius: 50%;
  font-family: sans-serif;
  font-size: 2rem;
  font-weight: 700;
  text-shadow: 0 0.1rem 0.1rem rgba(0,0, 0, 0.5);

  color: #ac550e;
  letter-spacing: 0.4rem;
  text-transform: uppercase;
  text-shadow: 0 0 0.1rem #ac550e;
  box-shadow: .3rem .3rem .6rem var(--greyDark-1), 
             -.2rem -.2rem .5rem var(--white) !important;
  position: relative;           
}
.ring:before{
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0.3rem solid transparent;
  border-top: 0.3rem solid var(--loader);
  border-right: 0.3rem solid var(--loader);
  border-radius: 50%;
  animation: animateCircle 2s linear infinite;
}
.ring>span{
  display: block;
  position: absolute;
  right: 0;
  background: var(--loader);
  background: transparent;
  width: 50%;
  height: 0.5rem;
  transform-origin: left;
  animation: animate 2s linear infinite;
}
.ring>span:before{
  content: "";
  position: absolute;
  width: 1.6rem;
  height: 1.6rem;
  border-radius: 50%;
  background: var(--loader);
  right: -0.7rem;
  top: -0.6rem;
  box-shadow: 0 0 2rem var(--loader);
}

@keyframes animateCircle {
  0%{
    transform: rotate(0deg);
  }
  100%{
    transform: rotate(360deg)
  }
}
@keyframes animate {
  0%{
    transform: rotate(50deg);
  }
  100%{
    transform: rotate(405deg)
  }
}
.loading-container{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  position: fixed;
}


}


